import Vue, { VNode } from "vue";
import { mergeData } from "vue-functional-data-merge";

const justifyProp = {
  type: String,
  default: "",
  validator: (v: string) =>
    !v ||
    [
      "space-between",
      "space-around",
      "center",
      "left",
      "right",
      "stretch",
      "end",
      "start",
      "flex-end",
      "flex-start",
    ].includes(v),
};

const alignProp = {
  type: String,
  default: "",
  validator: (v: string) =>
    !v ||
    ["center", "flex-end", "flex-start", "baseline", "stretch"].includes(v),
};

export default Vue.extend({
  name: "AsRow",
  functional: true,
  props: {
    tag: {
      type: String,
      default: "div",
    },
    justify: justifyProp,
    justifySm: justifyProp,
    justifyMd: justifyProp,
    justifyLg: justifyProp,
    justifyXl: justifyProp,
    align: alignProp,
    alignSm: alignProp,
    alignMd: alignProp,
    alignLg: alignProp,
    alignXl: alignProp,
    direction: {
      type: String,
      default: "",
      validator: v =>
        !v || ["column", "column-reverse", "row", "row-reverse"].includes(v),
    },
    nowrap: Boolean,
    gutters: {
      type: [Boolean, String],
      default: false,
      validator: v =>
        typeof v === "boolean" ||
        ["x-small", "small", "medium", "large", "x-large"].includes(v),
    },
  },
  render(h, { data, props, children }): VNode {
    const {
      tag,
      justify,
      justifySm,
      justifyMd,
      justifyLg,
      justifyXl,
      align,
      alignSm,
      alignMd,
      alignLg,
      alignXl,
      direction,
      nowrap,
      gutters,
    } = props;

    const componentData = {
      class: [
        "as-row",
        {
          ["is-justify-" + justify]: justify,
          ["is-justify-sm-" + justifySm]: justifySm,
          ["is-justify-md-" + justifyMd]: justifyMd,
          ["is-justify-lg-" + justifyLg]: justifyLg,
          ["is-justify-xl-" + justifyXl]: justifyXl,
          ["is-align-" + align]: align,
          ["is-align-sm-" + alignSm]: alignSm,
          ["is-align-md-" + alignMd]: alignMd,
          ["is-align-lg-" + alignLg]: alignLg,
          ["is-align-xl-" + alignXl]: alignXl,
          ["is-direction-" + direction]: direction,
          "is-gutters-medium": typeof gutters === "boolean" && gutters,
          ["is-gutters-" + gutters]: typeof gutters === "string",
          "is-nowrap": nowrap,
        },
      ],
    };

    return h(tag, mergeData(data, componentData), children);
  },
});
