import Vue, { VueConstructor, VNode } from "vue";
import { mergeData } from "vue-functional-data-merge";
import Circle from "./circle.svg";
import Dots from "./dots.svg";

const shapesMatches: Record<string, VueConstructor<Vue>> = {
  circle: Circle,
  dots: Dots,
};

export default Vue.extend({
  name: "AsLoader",
  functional: true,
  props: {
    tag: {
      type: String,
      default: "div",
    },
    size: {
      type: String,
      default: "md",
      validator: (v: string) => ["xs", "sm", "md", "lg", "xl"].includes(v),
    },
    shape: {
      type: String,
      default: "circle",
      validator: (v: string) => Object.keys(shapesMatches).includes(v),
    },
    overlay: {
      type: String,
      default: undefined,
      validator: (v: string | undefined) =>
        !v || ["container", "page"].includes(v),
    },
  },
  render(h, { data, props }): VNode {
    const { tag, size, shape, overlay } = props;
    const Shape = shapesMatches[shape];
    const componentData = {
      class: [
        "as-loader",
        "is-" + size,
        {
          "is-container-overlay": overlay === "container",
          "is-page-overlay": overlay === "page",
        },
      ],
    };

    return h(tag, mergeData(data, componentData), [h(Shape)]);
  },
});
