import Vue, { VNode } from "vue";
import { mergeData } from "vue-functional-data-merge";

const colsProp = {
  type: [Number, String],
  default: 0,
};

const offsetProp = {
  type: [Number, String],
  default: 0,
};

export default Vue.extend({
  name: "AsCol",
  functional: true,
  props: {
    tag: {
      type: String,
      default: "div",
    },
    cols: colsProp,
    sm: colsProp,
    md: colsProp,
    lg: colsProp,
    xl: colsProp,
    offset: offsetProp,
    offsetSm: offsetProp,
    offsetMd: offsetProp,
    offsetLg: offsetProp,
    offsetXl: offsetProp,
    first: Boolean,
    firstSm: Boolean,
    firstMd: Boolean,
    firstLg: Boolean,
    firstXl: Boolean,
    last: Boolean,
    lastSm: Boolean,
    lastMd: Boolean,
    lastLg: Boolean,
    lastXl: Boolean,
  },
  render(h, { data, props, children }): VNode {
    const {
      tag,
      cols,
      sm,
      md,
      lg,
      xl,
      offset,
      offsetSm,
      offsetMd,
      offsetLg,
      offsetXl,
      first,
      firstSm,
      firstMd,
      firstLg,
      firstXl,
      last,
      lastSm,
      lastMd,
      lastLg,
      lastXl,
    } = props;

    const componentData = {
      class: [
        "as-col",
        {
          ["is-" + cols]: cols,
          ["is-sm-" + sm]: sm,
          ["is-md-" + md]: md,
          ["is-lg-" + lg]: lg,
          ["is-xl-" + xl]: xl,
          ["is-offset-" + offset]: offset,
          ["is-sm-offset-" + offsetSm]: offsetSm,
          ["is-md-offset-" + offsetMd]: offsetMd,
          ["is-lg-offset-" + offsetLg]: offsetLg,
          ["is-xl-offset-" + offsetXl]: offsetXl,
          "is-first": first,
          "is-first-sm": firstSm,
          "is-first-md": firstMd,
          "is-first-lg": firstLg,
          "is-first-xl": firstXl,
          "is-last": last,
          "is-last-sm": lastSm,
          "is-last-md": lastMd,
          "is-last-lg": lastLg,
          "is-last-xl": lastXl,
        },
      ],
    };

    return h(tag, mergeData(data, componentData), children);
  },
});
