import Vue, { VNode } from "vue";
import { mergeData } from "vue-functional-data-merge";
import AsLoader from "../loader";

export default Vue.extend({
  name: "AsButton",
  functional: true,
  props: {
    tag: {
      type: String,
      default: "button",
      validator: (v: string) =>
        ["button", "a", "router-link", "nuxt-link", "div", "span"].includes(v),
    },
    href: {
      type: String,
      default: undefined,
    },
    to: {
      type: [Object, String],
      default: undefined,
    },
    theme: {
      type: String,
      default: "primary",
    },
    size: {
      type: String,
      default: "md",
    },
    loader: {
      type: String,
      validator: (v: string) => ["circle", "dots", "bars"].includes(v),
    },
    outline: Boolean,
    rounded: Boolean,
    circle: Boolean,
    loading: Boolean,
    disabled: Boolean,
    block: Boolean,
    pressed: Boolean,
    nuxt: Boolean,
  },
  render(h, { data, props, slots }): VNode {
    let Tag = props.tag;
    const {
      href,
      to,
      theme,
      size,
      loader,
      outline,
      rounded,
      circle,
      loading,
      disabled,
      block,
      pressed,
      nuxt,
    } = props;
    const {
      prepend: prependSlot,
      append: appendSlot,
      default: defaultSlot,
    } = slots();

    if (href) {
      Tag = "a";
    } else if (to && !nuxt) {
      Tag = "router-link";
    } else if (to && nuxt) {
      Tag = "nuxt-link";
    }

    const componentData = {
      class: [
        "as-button",
        "is-" + theme,
        "is-" + size,
        {
          "is-outline": outline,
          "is-rounded": rounded,
          "is-circle": circle,
          "is-disabled": disabled,
          "is-loading": loading,
          "is-pressed": pressed,
          "is-block": block,
        },
      ],
      attrs: {
        disabled: Tag === "button" && (disabled || loading),
        href: href || undefined,
        to: to || undefined,
      },
    };

    return (
      <Tag {...mergeData(data, componentData)}>
        {loading && <AsLoader shape={loader}></AsLoader>}
        {prependSlot && <span class="as-button-prepend">{prependSlot}</span>}
        {defaultSlot}
        {appendSlot && <span class="as-button-append">{appendSlot}</span>}
      </Tag>
    );
  },
});
